canvas.wave {
  position: absolute;
  bottom: 0px;
  left: 0px;
  width: 100%;
}

.nav_line_split {
  list-style: none;
  padding: 0;
}

.nav_line_split li {
  display: inline-block;
  padding: 0px 24px;
  border-right: solid 1px #3FBFBB;
  cursor: pointer;
  transition: 0.5s 0s, font-size 0.5s, line-height 0.5s;
}

.nav_line_split li:hover {
  color: #3FBFBB;
}

.nav_line_split li:last-child {
  border-right: none;
}

.nav_line_split li.active {
  color: #3FBFBB;
}

a {
  color: #3FBFBB;
}

.default_bg {
  background-image: url("../img/homepage/Home7.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}

.default_bg:before {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  background-color: rgba(255, 255, 255, 0.4);
}

.slide_bullet {
  list-style: none;
  position: fixed;
  right: 40px;
  top: 50%;
  transform: translate(-50%, 0px);
  z-index: 500;
}

.slide_bullet li {
  display: block;
  width: 9px;
  height: 9px;
  border-color: #777;
  border-radius: 50%;
  margin-bottom: 16px;
  cursor: pointer;
  transition: 0.5s 0s, font-size 0.5s, line-height 0.5s;
  position: relative;
}

.slide_bullet li:after {
  content: "";
  display: block;
  border-radius: 50%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 6px;
  height: 6px;
  background-color: #3e3e3e;
  transition: 0.5s 0s, font-size 0.5s, line-height 0.5s;
}

.slide_bullet li:hover {
  border-color: #3FBFBB;
}

.slide_bullet li:hover:after {
  background-color: #3FBFBB;
  width: 12px;
  height: 12px;
}

.slide_bullet li.active {
  background-color: #3FBFBB;
}

.slide_bullet li.active:after {
  background-color: #3FBFBB;
  width: 12px;
  height: 12px;
