.page_index {
  opacity: 1;
  .container.flex {
    height: 100vh; }


  section {
    min-height: 100vh;
    @media screen and (max-height: 900px) {
      min-height: 900px; }
    .section_title {
      margin-bottom: $gutter_width*2;
      letter-spacing: 3px; }
    &>.container.flex {
      @media screen and (max-height: 900px) {
        min-height: 900px; } } }

  .percent {
    font-family: 'Lato';
    font-weight: 300;
    font-size: 160px;
    // opacity: 0.4

    color: lighten($color_theme,24);
    letter-spacing: 0px;
    vertical-align: middle;
    // margin-bottom: 3*$gutter_width
    transform: skewX(-10deg);
    position: absolute;
    bottom: grid_sz(0,5);
    right: grid_sz(0,3);
    vertical-align: middle;
    white-space: nowrap;
    @include flex_center;

    span {
      font-weight: 300;
      vertical-align: middle;
      display: block; }
    canvas {
      width: 200%;
      @include ab_center; }
    @include trans;
    &.initial {
      opacity: 0; }
    span:after {
      font-family: "Lato"," L" , 'Noto Sans TC','';
      display: inline-block;

      content: "";
      font-weight: 400;
      font-size: grid_sz(0,2.2);
      vertical-align: middle;

 } }      // padding-bottom: 50px


  .page_index_live .percent {
    span:after {
      content: ""; } }


  //-----------------------------------------------
  .page_index_main {
    $news_height: grid_sz(0,12.5);
    background-image: url("../img/homepage/Home1.jpg");
    min-height: 100vh;
    @include flex_split("td");
    align-items: center;
    align-content: center;
    justify-content: space-between;
    background-position: center 0px;
    @include rwd_sm {
      height: auto;
      padding-bottom: 0; }


    .row {
      position: relative; }

    .index_slogan_area {
      height: calc(100vh - #{$news_height});
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      margin: auto;
      left: 50%;
      padding-top: grid_sz(0,3);
      transform: translateX(-50%);
      @include flex_center;
      flex-direction: column;
      @include rwd_sm {
        position: relative;
        height: auto; } }

    h1 {
      letter-spacing: 15px;
      margin-bottom: 0px;
      margin-top: 0px;
      animation: lspace_in 2s 1 both;
      text-align: center;
      span {
        font-weight: 300; } }

    @keyframes lspace_in {
      0% {
        letter-spacing: 10px;
        opacity: 0; }
      100% {
        letter-spacing: 15px;
        opacity: 1; } }

    h3.page_header_eng {
      margin-top: 10px;
      font-size: 30px;
      letter-spacing: 2px;
      animation: lspace_in2 2s 0.5s 1 both;
      text-align: center;
      font-weight: 300; }

    @keyframes lspace_in2 {
      0% {
        letter-spacing: 0px;
        opacity: 0; }
      100% {
        letter-spacing: 2px;
        opacity: 1; } }

    @include rwd_md {
      background-size: 120% auto;
      .news_container {
        width: 100%; } }

    .news_container {
      position: absolute;
      bottom: 0px;
      width: grid_sz(10,9);
      height: $news_height;

      transform: translateX(-50%);
      left: 50%;
      @include box_shadow;

      @include rwd_sm {
        position: relative;
        width: 100%; } }


    .news {
      display: flex;
      flex-direction: row;
      background-color: $color_theme;
      height: 100%;

      opacity: 1;




      .col_infos,.col_img {
        height: 100%;
        position: relative; }

      .col_infos {
        width: grid_sz(6,6);
        padding: grid_sz(0,1);
        display: flex;
        flex-direction: column;
        justify-content: space-between; }

      .col_img {
        width: grid_sz(4,3); }

      .col_infos .btns {
        @include flex_split("lr");
        width: 100%;
        z-index: 10;

 }        // height: 200px
      h4,h5,p {
        color: $color_white;
        margin: 0;
        font-weight: 300; }
      h5 {
        margin-bottom: $gutter_width; }
      .cover_img {
        background-image: url("../img/homepage/Home2.jpg");
        background-position: center center;
        background-size: auto 110%;
        height: 200px+$gutter_width*3;
        margin-right: 0px;
 }        // transition:  background-image 0.5s

      @include rwd_md {
        .col_infos {
          width: 70%; }
        .col_img {
          width: 30%; } } } }


  //-----------------------------------------------

  .page_index_grow {
    background-image: url("../img/homepage/Home3.jpg");
    background-size: cover;

    @include rwd_sm {
      &:before {
        @include pseudo_ab_full;
        background-color: rgba(white,0.8); } } }


  //-----------------------------------------------

  .page_index_live {
    background-image: url("../img/homepage/Home4b.jpg");

    .btn {
      margin-top: $gutter_width*2; }

    .percent {
      position: absolute;
      right: 0;
      left: auto;
      bottom: grid_sz(0,5); }

    @include rwd_md {
      &:before {
        @include pseudo_ab_full;
        background-color: rgba(white,0.8); } } }
  //----------------------------------------------
  .page_index_accurate {
    background-image: url("../img/homepage/Home5.jpg");
    position: relative;
    display: flex;
    justify-content: center;
    min-height: 100vh;



    .btn-primary {
      border: solid 1px white;
      background-color: transparent;
      &:hover {
        background-color: #FFF;
        color: $color_grey; } }

    .section_title,.section_para {
      color: $color_white;
      * {
        color: $color_white; } }

    .col_right {
      padding-top: grid_sz(0,2);
      padding-bottom: grid_sz(0,8); }

    .btn {
      margin-top: $gutter_width*2; }


    @include bg_block('before','right',$color_theme);


    .col-sm-6 {
      padding-left: 1.5*$gutter_width;
      padding-right: 1.5*$gutter_width;
      text-align: center;
      h3,p {
        color: $color_white; } }

    .btn {
      margin-bottom: 0; }

    .acc_sm_pic {
      // background-color: $color_theme
      border: none;
      width: 50%;

      height: 100%;
      cursor: pointer;



      &:hover {
        &:before {
          opacity: 1; } }
      &:before {
        @include pseudo_ab_full;
        @include ab_center;
        background-color: rgba($color_theme,0.5);
        opacity: 0;
        transition: 0.5s;
        z-index: 2; }
      &:after {
        @include pseudo_ab_full;
        @include ab_center;
        background-size: cover;
        z-index: 1; }
      h4 {
        color: $color_white;
        font-size: 20px;
        letter-spacing: 3px;
        @include ab_center;
        z-index: 3; } }

    .acc_sm_pic[data-type="1"]:after {
      background-image: url("../img/homepage/Home6-1.jpg"); }
    .acc_sm_pic[data-type="2"]:after {
      background-image: url("../img/homepage/Home6-4.jpg"); }
    .acc_sm_pic[data-type="3"]:after {
      background-image: url("../img/homepage/Home6-2.jpg"); }
    .acc_sm_pic[data-type="4"]:after {
      background-image: url("../img/homepage/Home6-3.jpg"); }


    .type_container {
      position: absolute;
      bottom: 0px;
      height: grid_sz(0,14);
      flex-direction: column;
      min-height: 0px !important;

      z-index: 100;

      @include rwd_md {
        height: grid_sz(0,14); }
      @include rwd_xs {
        height: grid_sz(0,12); }
      .row {
        display: flex;
        @include size(grid_sz(8,8),50%);
        &:nth-child(1) {
          left: grid_sz(4,4); }
        &:nth-child(2) {
          left: grid_sz(2,2); }

        @include rwd_md {
          flex-direction: row;
          height: grid_sz(0,7);
          // height: 50%
          width: 100%;

          &:nth-child(1) {
            left: 0; }
          &:nth-child(2) {
            left: 0; } } } }

    @include rwd_sm {
      &:before {
        @include pseudo_ab_full;
        background-color: rgba($color_theme,0.8);
        .acc_sm_pic {
          height: grid_sz(0,7); } } } }

  //------------------------------------------------------
  .page_index_sponsor {
    min-height: initial;
    background-color: $color_grey_bg;
    .item {
      width: 25%; }

    .container {
      height: auto;
      padding-top: grid_sz(0,3);
      padding-bottom: grid_sz(0,2);
      justify-content: space-around;
      margin: auto; }

    .sponsorlogo {
      width: grid_sz(2,2);
      margin-left: auto;
      margin-right: auto;
      text-align: center;
      // padding-left: grid_sz(0,1)
      // padding-right: grid_sz(0,1)

      mix-blend-mode: darken; } }

  @include rwd_sm {

    section {
      min-height: 100vh;
      height: auto; }

    .page_index_live,.page_index_grow {
      height: auto;
      .col_left,.col_right {
        height: auto; }
      .section_title {
        margin-top: grid_sz(0,1); }
      .percent {
        font-size: 100px;
        right: 0;
        bottom: 0;
        position: relative;
        margin-top: grid_sz(0,4);
        // margin-bottom: grid_sz(0,0)
        &:after {
          font-size: 60px; } } }


    .page_index_main {
      // min-height: 150vh
      background-size: cover;

      .news .col_infos {
       padding-bottom: 0; }

      .index_slogan_area {
        height: 100vh;
        min-height: 100vh;
        h1 {
          font-size: 32px;
          span {
            display: block; } }
        h3 {
          font-size: 22px; } }

      .news_container {
        width: 100%;
        height: auto;
        position: relative;
        background-color: $color_theme;

        .news {
          flex-direction: column;
          height: auto;

          .btns {
            position: relative;
            margin-bottom: grid_sz(0,1);
            margin-top: grid_sz(0,1); }

          .col_img {
            order: -1;
            width: 100%;
            height: 200px; }
          .col_infos {
            width: 100%;
            height: auto;
            min-height: 350px; } } } }


    .page_index_grow {
      .col_left {
        display: none; } }

    .page_index_live {
      .col_right {
        min-height: initial;
        height: 0px; } }


    .page_index_accurate {
      min-height: 130vh;
      display: block;
      align-items: flex-start;

      .acc_sm_pic {
        white-space: nowrap; }
      .container.flex {
        align-items: flex-start;
        .col_left {
          display: none; }
        .col_right {
          display: block;
          padding-top: grid_sz(0,3);
          padding-bottom: grid_sz(0,3);
          .section_title {
            margin-top: 0px; } } } } } }







