.page_about {
  .container {
 }    // +flex_center

  * {
 }    // border: solid 1px
  section {
    min-height: 0px;
    .section_title {
      padding-top: $gutter_width*0;
      margin-top: 0;
      margin-bottom: $gutter_width*2;
      opacity: 0.8;
      letter-spacing: 10px; } }



  a {
    text-decoration: none; }

  nav.navbar-default.at_top {
    background-color: #FFF; }
  ul.nav_line_split {
    margin-bottom: grid_sz(0,2); }

  .section_hero {
    min-height: 100vh;
    display: flex;
    align-items: center;
    overflow: hidden;

    .mountain {
      background: url("../img/homepage/About1B.jpg");
      background-size: 100% auto;
      background-position: center bottom;
      width: 100%;
      position: absolute;
      height: 4000px;
      bottom: 0;
      padding-top: 100%;
      background-repeat: no-repeat;

      @include rwd_md {
        background-size: 150% auto;
        height: 4000px;
        background-position: 60% bottom; }
      @include rwd_sm {
        background-size: 200% auto;
        height: 4000px;
        background-position: 65% bottom; }
      @include rwd_xs {
        background-size: 350% auto;
        height: 4000px;
        background-position: 65% bottom; } }

    .col_hero {
      h1,p {
        margin-left: auto;
        margin-right: auto; } }
    .container {
      display: flex;
      flex-direction: column;
      align-items: center; }


    // background-repeat: no-repeat
    padding-bottom: grid_sz(0,23);
    background-color: #0170c2;

    .section_title {
      margin-top: grid_sz(0,13);
      @include rwd_sm {
        margin-top: grid_sz(0,7); } }


    &,h1 {
      color: $color_white;
      opacity: 0.95; }
    h1 {
      margin-bottom: $gutter_width*2; }
    p {
      color: $color_white;
      font-weight: 300;
      opacity: 0.9; } }


  .section_title {
    padding-top: $gutter_width*4; }

  .section_log {

    .section_title {
      text-shadow: 0px 0px 10px rgba(255, 255, 255, 1); }

    .logs_area {
      background-color: $color_white;
      padding: grid_sz(0,1);
      width: 100%; }

    .container {
      margin-top: grid_sz(0,-12); }


    .log_box {
      margin-bottom: $gutter_width;
      display: flex;
      cursor: pointer;
      * {
        color: $color_grey_light; }

      .title {
        color: $color_grey_dark;
        margin-bottom: grid_sz(0,1); } }


    .col_cover {
      width: percent_grid(4,4,12,13); }
    .col_info {
      width: percent_grid(8,8,12,13);
      padding: grid_sz(0,1);
      padding-left: 0px;

      margin-left: grid_sz(0,1);
      border-bottom: solid 1px $color_theme; }

    @include range_width(0px,800px) {
      .col_info,.col_cover {
        width: 100%; }
      .col_info {
        padding-right: 0px;
        margin: 0; }
      .col_cover {
        min-height: 120px; }
      .cover_image {
        height: 100%;

        min-height: 200px; }
      .log_box {
        flex-direction: column; } } }


  .cover_image {
    min-height: 200px;
    background-size: cover;

    &[data-log="1"] {
      background: url("../img/homepage/Post2.jpg");
      background-size: cover; }
    &[data-log="2"] {
      background: url("../img/homepage/Post3.jpg");
      background-size: cover; }
    &[data-log="3"] {
      background: url("../img/homepage/Post4.jpg");
      background-size: cover; } }





  .person_box {
    padding: $gutter_width*2 $gutter_width*1;
    position: relative;
    display: flex;
    .row {
      position: relative;
      display: flex; }
    font-weight: 800;
    color: rgba(black,0.6);
    h4 {
      font-weight: bold; }
    .bigger_text {
      font-size: 1.1em; }
    ul {
      list-style: none;
      padding: 0;
      li {
        padding-top: 3px;
        padding-bottom: 3px;
        &:before {
          content: ".";
          position: relative;
          display: inline-block;
          top: -5px;
          margin-right: 15px; } } } } }
