// color variables
$color_theme: #3FBFBB;
$color_white: #fff;
$color_grey: #555;
$color_grey_light: #777;
$color_grey_dark: #333;
$color_grey_bg: #f4f5f5;

$gutter_width: 24px;
$column_width: 72px;
$nav_height: 80px;

//mixins
@mixin size($w,$h:$w) {
  width: $w;
  height: $h; }

@mixin pos($x,$y) {
  left: $x;
  top: $y; }

@mixin ab_pos($x,$y) {
  position: absolute;
  left: $x;
  top: $y; }

@mixin flex_split($mode) {
  display: flex;
  justify-content: space-between;

  @if ($mode=="lr") {
    flex-direction: row; }
  @if ($mode=="td") {
    flex-direction: column; } }


@mixin flex_center {
  display: flex;
  justify-content: center;
  align-items: center; }

@mixin full {
  @include size(100%,100%); }

.full {
  @include full; }

@mixin ab_full {
  position: absolute;
  left: 0;
  top: 0;
  @include size(100%,100%); }

@mixin pseudo_ab_full {
  content: "";
  display: block;
  @include ab_full; }

@function percent_grid($a,$b:0,$c:12,$d:13) {
  @return percent(grid_sz($a,$b),grid_sz($c,$d)); }

@function percent($a,$b) {
  @return $a/$b*100%; }

@function grid_sz($c,$g:0,$p:0) {
  @return $c*$column_width+$g*$gutter_width+$p; }

@mixin paragraph_size_leading($fz,$lh:$fz*1.5,$sp:0) {
  font-size: $fz;
  line-height: $lh;
  letter-spacing: $sp; }

@mixin ab_center {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%); }

.ab_center {
  @include ab_center; }

@mixin flex_center {
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 1 1 0%; }

@mixin bg_theme {
  background-color: $color_theme; }

@mixin colors_bg_text($c1,$c2: false,$c3: false) {
  background-color: $c1;
  @if ($c2) {
    color: $c2; }
  @if ($c3) {
    border-color: $c3; } }


.bg_theme {
  @include bg_theme; }

@mixin bg_grey {
  background-color: $color_grey; }

.bg_grey {
  @include bg_grey; }

@mixin bg_white {
  background-color: $color_white; }

.bg_white {
  @include bg_white; }

.border_theme {
  border: solid 1px $color_theme; }

@mixin box_shadow {
  box-shadow: 0px 0px 20px rgba(0,0,0,.3); }

@mixin pos_fixed_bottom {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%); }

@mixin bg_block($el:'before',$place:'left',$color: $color_theme) {
  &:#{$el} {
    content: "";
    display: block;
    width: 50%;
    height: 100%;
    position: absolute;
    @if ($place=='left') {
      left: 0px; }
    @else {
      right: 0px; }

    top: 0;
    background-color: $color;
    opacity: 0.8;
    @include range_width(0px,800px) {
      width: 100%;
      height: 50%;
      left: 0;

      @if ($place=='right') {
        top: 0px; }
      @else {
        top: initial;
        bottom: 0px; } } } }


.size_full {
  @include size(100%,100%); }

//RWD

@mixin range_width($min: 0,$max: $min) {
  @if ($max==$min) {
    @media screen and (min-width: $min) {
      @content; } }
  @else {
    @media screen and (max-width: $max - 1px) and (min-width: $min) {
      @content; } } }


@mixin range_height($min: 0,$max: $min) {
  @if ($max==$min) {
    @media screen and (min-height: $min) {
      @content; } }
  @else {
    @media screen and (max-height: $max - 1px) and (min-height: $min) {
      @content; } } }



@mixin trans($t: 0.5s,$td: 0s) {
  transition: $t $td,font-size 0.5s,line-height 0.5s; }

@mixin grid_sz_mx($i,$o) {
  max-width: grid_sz($i,$o);
  width: 100%; }

$break_lg: 1600px;
$break_md: 1200px;
$break_sm: 800px;
$break_xs: 500px;

$grid_gutter: 24px;

@mixin rwd_md {
  @include range_width(0px,$break_md) {
    @content; } }
@mixin rwd_lg {
  @include range_width(0px,$break_lg) {
    @content; } }
@mixin rwd_sm {
  @include range_width(0px,$break_sm) {
    @content; } }
@mixin rwd_xs {
  @include range_width(0px,$break_xs) {
    @content; } }



@mixin cover_opacity($color: black) {
  display: block;
  content: "";
  position: absolute;
  @include size(100%,100%);
  left: 0;
  top: 0;
  background-color: $color; }


